}
.subtitle{
    font-size: 25;
    border: 1px solid black;
    box-shadow: 4px 4px 2px black;
    padding: 5px 10px;
}
.quizInfo{
    width: 90%;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    margin: 10px;
}